.crm-container.ui-dialog{border:none;border-radius:2px;box-shadow:0 3px 18px 0 rgba(49,40,40,0.25);color:#4D5663;font-family:"Open Sans","Helvetica Neue",Helvetica,Arial,sans-serif;padding:0}.crm-container.ui-dialog .ui-dialog-header,.crm-container.ui-dialog .ui-dialog-titlebar{background:#F3F6F7;border:none;border-radius:0;color:#4D5663;font-size:18px;font-weight:600;padding:14px 20px}.crm-container.ui-dialog .ui-dialog-header .ui-dialog-title,.crm-container.ui-dialog .ui-dialog-titlebar .ui-dialog-title{background:0;padding:0}.crm-container.ui-dialog .ui-dialog-header .ui-button,.crm-container.ui-dialog .ui-dialog-titlebar .ui-button{background:none;line-height:30px;outline:0}.crm-container.ui-dialog .ui-dialog-header .ui-button .ui-icon,.crm-container.ui-dialog .ui-dialog-titlebar .ui-button .ui-icon{background:none;overflow:visible;text-indent:0;top:0}.crm-container.ui-dialog .ui-dialog-header .ui-button .ui-icon.ui-icon-closethick::after,.crm-container.ui-dialog .ui-dialog-titlebar .ui-button .ui-icon.ui-icon-closethick::after{font-family:'FontAwesome';font-style:normal;text-rendering:auto;font-size:13px;content:"";color:#727E8A}.crm-container.ui-dialog .ui-dialog-header .ui-button .ui-icon.ui-icon-newwin::after,.crm-container.ui-dialog .ui-dialog-titlebar .ui-button .ui-icon.ui-icon-newwin::after{font-family:'FontAwesome';font-style:normal;text-rendering:auto;font-size:13px;content:"";color:#727E8A}.crm-container.ui-dialog .ui-dialog-content#bootstrap-theme,.crm-container.ui-dialog .ui-dialog-content[id*="crm-ajax-dialog"]{background:#fff;padding:10px 20px}.crm-container.ui-dialog .ui-dialog-buttonpane{background:#fff;border-top:1px solid #F3F6F7;padding:15px 10px}.crm-container.ui-dialog .ui-dialog-buttonpane .ui-button{background:#fff;border:1px solid #979797;border-radius:2px;color:#4D5663;padding:0;text-align:center;text-shadow:none;text-transform:uppercase}.crm-container.ui-dialog .ui-dialog-buttonpane .ui-button .ui-button-text{padding:0.7em 1.3em}.crm-container.ui-dialog .ui-dialog-buttonpane .ui-button .ui-icon{display:none}.crm-mosaico-page .helpicon{background-color:#42AFCB}
